<template>
  <div class="container">
    <div v-if="loading" class="waiting">
      <img src="../assets/puff.svg" alt="loading-image" />
    </div>
    <div v-else-if="errorMessage" class="alert alert-danger alert-dismissable">
      {{ errorMessage }}
    </div>
    <div v-else>
      <div v-if="successMessage" class="alert alert-success alert-dismissable">
        {{ successMessage }}
      </div>
      <div class="table-responsive-sm">
        <h3>User</h3>
        <table class="table table-striped">
          <thead>
            <tr>
              <th>Id</th>
              <th>Username</th>
              <th>Mail</th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>{{ user.id }}</td>
              <td>{{ user.username }}</td>
              <td>{{ user.email }}</td>
              <td></td>
              <td></td>
            </tr>
          </tbody>
        </table>
      </div>
      <div v-if="courses.length" class="table-responsive-sm">
        <h3>Courses</h3>
        <table class="table table-striped">
          <thead>
            <tr>
              <th>Id</th>
              <th>Description</th>
              <th>Language</th>
              <th>Type</th>
              <th>Price</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="course in courses" :key="course.id">
              <td>{{ course.id }}</td>
              <td>{{ course.description }}</td>
              <td>{{ course.language }}</td>
              <td>{{ course.type }}</td>
              <td>{{ course.price }}</td>
              <td>
                <button
                  class="btn btn-danger"
                  @click="deleteCourseClicked(course.id)"
                >
                  <trash-icon />
                </button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div v-else>
        No se han encontrado resultados para la búsqueda solicitada
      </div>
    </div>
  </div>
</template>

<script>
import UserDataService from "../service/UserDataService";
import CourseDataService from "../service/CourseDataService";
// import trashIcon from "@/components/TrashIcon.vue";
import TrashIcon from '../components/TrashIcon.vue';

export default {
  name: "UserDetails",
  components: {
    TrashIcon
  },

  data() {
    return {
      courses: [],
      user: {},
      loading: false,
      errorMessage: "",
      successMessage: ""
    };
  },
  computed: {
    id() {
      return this.$route.params.id;
    },
  },
  methods: {
    async refreshUserDetails() {
      const res = await UserDataService.retrieveUser(this.id);
      this.user = res.data;
    },
    async getCoursesByUser() {
      try {
        this.loading = true;
        const response = await UserDataService.retrieveCoursesByUser(this.id);
        this.courses = response.data;
      } catch (error) {
        this.errorMessage = "Couldn't get data, try it again later";
      } finally {
        this.loading = false;
      }
    },
    async deleteCourseClicked(courseId) {
      const result = await CourseDataService.retrieveCourse(courseId);
      const course = result.data;
      const res = await UserDataService.removeCourseToUser(this.id, course);
      if (res.status === 200) {
        this.successMessage = `Delete of course Successful`;
      };
      setTimeout(() => { this.successMessage = ""}, 3000);
      this.getCoursesByUser();
    },
    backCourses() {
      this.$router.push({ name: "Users" });
    },
  },
  created() {
    this.refreshUserDetails();
    this.getCoursesByUser();
  },
};
</script>

<style scoped>
button {
  display: flex;
  align-items: center;
}
</style>